footer{
    background: var(--color-primary);
    padding: 2.5rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a{
    color: var(--color-bg);
}

.footerLogo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permaLinks{
    display: flex;
    flex-wrap: wrap ;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footerSocials{
    display:  flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footerSocials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footerSocials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footerCopyright{
    margin-bottom: 4rem ;
    color: var(--color-bg);
}
/*media query */
/* small device */
@media screen and (max-width:600px) {
    .permaLinks{
        flex-direction: column;
        gap: 1.5rem;
    }
    .footerSocials{
        margin-bottom: 2.6rem;
    }
}