.experienceContainer{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.experienceContainer > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
}
.experienceContainer > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.experienceContainer > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}
.experienceContent{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap:2rem;
}
.experirnceDetails{
    display: flex;
    gap: 1rem;
}
.experirnceDetails-icon{
    margin-top: 6px;
    color:  var(--color-primary);
}

/* ====media query */
/* ====medium device */
@media screen and (max-width: 1024px) {
    .experienceContainer{
        grid-template-columns: 1fr;
    }

    .experienceContainer > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .experienceContent{
        padding: 1rem;
    }
}

/* ====small device */
@media screen and (max-width: 600px) {
    .experienceContainer{
        gap: 1rem;
    }

    .experienceContainer > div {
        width: 100%;
        padding: 2rem 1rem;
    }
}